import { url } from '../Enviroments/Enviroments'

export class AutenticationClass {

    token;

    async iniciarSesion(objUser) {
        let misHeaders = new Headers();
        misHeaders.append("Content-Type", "application/json")
        let config = {
            method: 'POST',
            headers: misHeaders,
            body: JSON.stringify(objUser)
        }
        let firstRpta = await fetch(`${url}/login`, config)
        let rpta = await (firstRpta).json()
        return ([firstRpta, rpta]);
    }
    guardarToken(token) {
        this.token = token
        localStorage.setItem('jercourier', token);
    }
    cerrarSesion() {
        localStorage.removeItem('jercourier');
        this.token = null
    }
    estaLogeado() {
        if (localStorage.getItem('jercourier')) {
            this.token = localStorage.getItem('jercourier')
        }
        if (this.token) {
            try {
                let payload = this.token.split(".")[1];
                // console.log('payload',payload)
                let payloadDecodificado = window.atob(payload);
                // console.log('payloadDecodificado',payloadDecodificado)
                let payloadJSON = JSON.parse(payloadDecodificado);
                // console.log('payloadJSON',payloadJSON);
                if (payloadJSON.exp > new Date() / 1000) {
                    return true
                } else {
                    this.cerrarSesion();
                    return false;
                }
            } catch (error) {
                this.cerrarSesion();
                return false;
            }
        } else {
            console.log("No hay token en el localstorage");
            return false;
        }
    }
}