import React, { useState, useEffect, Fragment } from 'react'
import { makeStyles } from '@material-ui/core/styles';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import Button from '@material-ui/core/Button';
import { MDBCard, MDBCardBody, MDBCardTitle } from 'mdbreact'
import { EnvioClass } from '../../../Services/EnvioServices'
import Swal from 'sweetalert2'
import { Title } from '@material-ui/icons';
import { DepartamentoClass } from '../../../Services/DepartamentoServices';
import { ProvinciaClass } from '../../../Services/ProvinciasServices';
import { DistritoClass } from '../../../Services/DistritoServices';

const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  backButton: {
    marginRight: theme.spacing(1),
  },
  instructions: {
    marginTop: theme.spacing(1),
    marginBottom: theme.spacing(1),
  },
}));

function getSteps() {
  return ['Registrar Paquete', 'Destinatario', 'Confirmar Datos'];
}

function getStepContent(stepIndex) {
  switch (stepIndex) {
    case 0:
      return 'Select campaign settings...';
    case 1:
      return 'What is an ad group anyways?';
    case 2:
      return 'This is the bit I really care about!';
    default:
      return 'Unknown stepIndex';
  }
}

export default function EnviarPedido() {

  const [departamentos, setDepartamentos] = useState([])
  const [provincias, setProvincias] = useState([])
  const [distritos, setDistritos] = useState([])
  const [newEnvio, setNewEnvio] = useState({
    fecha: "2021-02-11 13:10:00",
    principal: true,
    ruta: false,
    destino: false,
    moto: false,
    usuId: 1,
    almaId: 1
  })
  console.log(newEnvio)
  const classes = useStyles();
  const [activeStep, setActiveStep] = useState(0);
  const steps = getSteps();

  useEffect(() => {
    DepartamentoClass.getDepartamentos().then(rpta => {
      if (rpta) {
        setDepartamentos(rpta)
      } else {
        alert("No found departamentos")
      }
    })
    ProvinciaClass.getProvincias().then(rpta => {
      if (rpta) {
        setProvincias(rpta)
      } else {
        alert("No found provincias")
      }
    })
    DistritoClass.getDistritos().then(rpta => {
      if (rpta) {
        setDistritos(rpta)
      } else {
        alert("N found distritos")
      }
    })
  }, [])

  const handleNext = () => {
    setActiveStep((prevActiveStep) => prevActiveStep + 1);
  };

  const handleBack = () => {
    setActiveStep((prevActiveStep) => prevActiveStep - 1);
  };

  const handleReset = () => {
    setActiveStep(0);
  };
  function newEnvioDni(e) {
    setNewEnvio({
      ...newEnvio,
      [e.currentTarget.name]: e.currentTarget.value
    })
  }
  function newEnvioString(e) {
    setNewEnvio({
      ...newEnvio,
      [e.currentTarget.name]: String(e.currentTarget.value).toUpperCase()
    })
  }
  function newEnvioInt(e) {
    setNewEnvio({
      ...newEnvio,
      [e.currentTarget.name]: parseInt(e.currentTarget.value)
    })
  }
  function POST_Envio() {
    EnvioClass.postEnvio(newEnvio).then(rpta => {
      if (rpta.ok === true) {
        Swal.fire({
          title: 'Felicidades',
          text: 'Tu envio se ha generado con éxito',
          icon: 'success'
        })
        handleNext()
      }
    })
  }

  function getForm() {
    if (activeStep === 0) {
      return (
        <Fragment>
          <MDBCardTitle>¿Qué envías?</MDBCardTitle>
          <form>
            <div className="form-group row">
              <div className="col-sm-3">
                <input type="text" className="form-control" style={{ border: '1px solid rgb(99, 99, 250)' }} id="clasificacion" name="clasificacion" placeholder="Artículo" value={newEnvio.clasificacion} onChange={newEnvioString} />
              </div>
              <div className="col-sm-6">
                <input type="text" maxLength="150" className="form-control" id="descripcion" name="descripcion" placeholder="Especifique" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.descripcion} onChange={newEnvioString} />
              </div>
              <div className="col-sm-3">
                <input type="number" min="0" step="0.01" className="form-control" id="precio" name="precio" placeholder="Valor S/" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.precio} onChange={newEnvioString} />
              </div>
            </div>
            <div className="form-group row">
              <div className="col-sm-3">
                <input type="number" min="0" step="0.01" className="form-control" id="largo" name="largo" placeholder="Largo" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.largo} onChange={newEnvioString} />
              </div>
              <div className="col-sm-3">
                <input type="number" min="0" step="0.01" className="form-control" id="ancho" name="ancho" placeholder="Ancho" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.ancho} onChange={newEnvioString} />
              </div>
              <div className="col-sm-3">
                <input type="number" min="0" step="0.01" className="form-control" id="alto" name="alto" placeholder="Altura" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.alto} onChange={newEnvioString} />
              </div>
              <div className="col-sm-3">
                <input type="number" min="0" step="0.01" className="form-control" id="peso" name="peso" placeholder="Peso kg" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.peso} onChange={newEnvioString} />
              </div>
            </div>
          </form>
          <div className="text-center mt-3">
            <Button variant="contained" color="primary" onClick={handleNext}>SIGUIENTE</Button>
          </div>
        </Fragment>
      )
    } else if (activeStep === 1) {
      return (
        <Fragment>
          <form>
            <MDBCardTitle>¿Quién Recibe?</MDBCardTitle>
            <div className="form-group row">
              <div className="col-sm-3">
                <input type="tel" maxLength="8" className="form-control" id="destDni" name="destDni" placeholder="DNI" style={{ border: '1px solid rgb(99, 99, 250)' }} onChange={newEnvioDni} />
              </div>
              <div className="col-sm-9">
                <input type="text" className="form-control" id="destName" name="destName" placeholder="Nombres" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.destName} onChange={newEnvioString} />
              </div>
            </div>
            <div className="form-group row">
              <div className="col-sm-3">
                <input type="tel" maxLength="9" className="form-control" id="destPhone" name="destPhone" placeholder="Teléfono" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.destPhone} onChange={newEnvioString} />
              </div>
              <div className="col-sm-4">
                <input type="text" className="form-control" id="destAdress" name="destAdress" placeholder="Dirección" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.destAdress} onChange={newEnvioString} />
              </div>
              <div className="col-sm-5">
                <input type="text" maxLength="100" className="form-control" id="destRef" name="destRef" placeholder="Referencia" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.destRef} onChange={newEnvioString} />
              </div>
            </div>
            <div className="form-group row">
              <div className="col-sm-3">
                <select className="form-control" name="DepaId" style={{ border: '1px solid rgb(99, 99, 250)' }} onChange={newEnvioString} >
                  <option>Departamento</option>
                  {
                    departamentos.map((data, key) => {
                      return <option key={key} value={data.name}>{data.name}</option>
                    })
                  }
                </select>
              </div>
              <div className="col-sm-4">
                <select className="form-control" name="ProvId" style={{ border: '1px solid rgb(99, 99, 250)' }} onChange={newEnvioString} >
                  <option>Provincia</option>
                  {
                    provincias.map((data, key) => {
                      return <option key={key} value={data.name}>{data.name}</option>
                    })
                  }
                </select>
              </div>
              <div className="col-sm-5">
                <select className="form-control" name="destId" style={{ border: '1px solid rgb(99, 99, 250)' }} value={newEnvio.destId} onChange={newEnvioInt} >
                  <option>Distrito</option>
                  {
                    distritos.map((data, key) => {
                      return <option key={key} value={data.id}>{data.name}</option>
                    })
                  }
                </select>
              </div>
            </div>
          </form>
          <div className="text-center mt-3">
            <Button disabled={activeStep === 0} onClick={handleBack} className={classes.backButton}>VOLVER</Button>
            <Button variant="contained" color="primary" onClick={handleNext}>SIGUIENTE</Button>
          </div>
        </Fragment>
      )
    } else if (activeStep === 2) {
      return (
        <>
          <table className="table">
            <thead>
              <tr>
                <th>RECIBE</th>
                <th>DESTINO</th>
                <th>DIRECCIÓN</th>
                <th>REFERENCIA</th>
                <th>OPCIONES</th>
              </tr>
            </thead>
            <tbody>
              <tr>
                <td>{newEnvio.destName}</td>
                <td>{newEnvio.DepaId} - {newEnvio.ProvId} - {
                  distritos.map(data => {
                    if (newEnvio.destId === data.id) {
                      return `${data.name}`
                    }
                  })
                }</td>
                <td>{newEnvio.destAdress}</td>
                <td>{newEnvio.destRef}</td>
                <td><i className="fas fa-edit" style={{ fontSize: '1.2em' }}></i> <i className="fas fa-trash" style={{ fontSize: '1.2em' }}></i></td>
              </tr>
            </tbody>
          </table>
          <div className="text-center mt-3">
            <Button disabled={activeStep === 0} onClick={handleBack} className={classes.backButton}>VOLVER</Button>
            <Button variant="contained" color="primary" onClick={() => POST_Envio()}>GUARDAR ENVIO</Button>
          </div>
        </>
      )
    } else {
      return (
        <Fragment>
          <MDBCardTitle>RESUMEN DE ENVIO</MDBCardTitle>
          <div className="text-center mt-3">
            <Button variant="contained" color="default" onClick={() => {
              document.location.href = "/dashboard/inicio"
            }}>FINALIZAR</Button>
          </div>
        </Fragment>
      )
    }
  }

  return (
    <div>
      <div className={classes.root}>
        <Stepper activeStep={activeStep} alternativeLabel>
          {steps.map((label) => (
            <Step key={label}>
              <StepLabel>{label}</StepLabel>
            </Step>
          ))}
        </Stepper>
      </div>
      <div className="mt-3">
        <MDBCard>
          <MDBCardBody>
            {getForm()}
          </MDBCardBody>
        </MDBCard>
      </div>
      {/* <div className="container mt-4">
                <div className="text-center">
                    {activeStep === steps.length ? (
                        <div>
                            <Typography className={classes.instructions}>All steps completed</Typography>
                            <Button onClick={handleReset}>MENU PRINCIPAL</Button>
                        </div>
                    ) : (
                            <div>
                                <Typography className={classes.instructions}>{getStepContent(activeStep)}</Typography>
                                <div>
                                    <Button disabled={activeStep === 0} onClick={handleBack} className={classes.backButton}>VOLVER</Button>
                                    <Button variant="contained" color="primary" onClick={handleNext}>{activeStep === steps.length - 1 ? 'FINALIZAR' : 'CONTINUAR'}</Button>
                                </div>
                            </div>
                        )}
                </div>
            </div> */}
    </div>
  )
}
